.dashboard {
  h2 {
    font-size: 22px;
    line-height: 28px;
    font-weight: normal;

    @include viewport--sm {
      font-size: 18px;
      line-height: 23px;
    }
  }
}

.dashboard__notifications {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-left: 11px;

  border: 1px solid $color-selago-1;
  border-radius: 50%;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-selago-2;
  }

  span {
    position: absolute;
    top: -5px;
    left: 69%;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    min-width: max-content;
    height: 20px;
    padding: 5px;

    color: $color-default-white;

    background-color: $color-rich-blue;
    border-radius: 10px;
  }

  @include viewport--sm {
    width: 39px;
    height: 39px;
    margin-left: 4px;

    span {
      left: 65%;
    }

    svg {
      height: 18px;
    }
  }
}

.dashboard__stats {
  max-width: calc(1003px + 8.3%);
  margin-bottom: 101px;
  margin-top: -2px;

  @include viewport--sm {
    margin-bottom: 34px;
    padding-top: 7px;
  }
}

.dashboard__premium {
  margin-top: auto;
  background-color: $color-rich-blue;
}
