.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.hidden {
  display: none;
}

.wrapper {
  position: relative;

  display: flex;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;

  &--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }

  @include viewport--md {
    flex-direction: column;
  }
}

.main-wrapper {
  margin-top: 45px;

  @include viewport--md {
    margin-top: 10px;
  }
}

.content-header {
  display: flex;
  align-items: center;
  height: 60px;
  margin-bottom: 39px;


  @include viewport--sm {
    min-height: 35px;
    height: auto;
    margin-bottom: 27px;
  }

  &--with-button {
    justify-content: space-between;

    @include viewport--sm {
      flex-direction: column;
      align-items: flex-start;
    }

    a {
      width: auto;
      min-width: 175px;

      &::before {
        content: "";
        width: 26px;
        height: 26px;
        margin-right: 10px;
        border-radius: 50%;
        border: 1px solid $color-selago-1;
      }

      @include viewport--sm {
        width: 100%;
        max-width: 100%;
        margin-top: 26px;
      }
    }
  }

  &--add {
    a {
      &::before {
        background: $color-default-white url("../img/sprite/icon-add.svg") center center no-repeat;
        background-size: 37%;
      }
    }
  }

  &--question {
    a {
      min-width: 165px;

      &::before {
        margin-right: 23px;
        margin-left: 5px;

        background: $color-default-white url("../img/sprite/icon-question.svg") center center no-repeat;
        background-size: 22%;

        @include viewport--sm {
          margin-right: 17px;
          margin-left: -5px;
        }
      }
    }
  }
}

.scroll-lock {
  position: fixed;

  overflow-y: auto;
}

.btn-reset {
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  max-width: 386px;
  width: 100%;
  height: 60px;

  font-size: 14px;
  line-height: 18px;
  border-radius: 10px;

  cursor: pointer;
}

.btn-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;

  background: $color-default-white;
  box-shadow: $box-shadow-mobile-menu;
  border-radius: 50%;
  transition: background-color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-selago-2;
  }

  &--dark {
    width: 30px;
    height: 30px;

    background: $color-night-rider;
    box-shadow: none;

    &:hover,
    &:focus,
    &:active {
      background-color: $color-rum;
    }
  }
}

.btn-rich-blue {
  color: $color-default-white;
  background-color: $color-rich-blue;
  transition: background-color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-blue-gem;
  }
}

.btn-white {
  color: $color-blackcurrant;
  background-color: $color-default-white;
  border: 1px solid $color-selago-1;
  transition: background-color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    background-color: $color-selago-2;
  }
}

.btn-open-menu {
  display: none;
  margin-right: 19px;
  padding-top: 7px;

  &:focus,
  &:hover {
    color: $color-rich-blue;
  }

  &:active {
    color: $color-blackcurrant;
  }

  @include viewport--md {
    display: initial;
  }
}

.section-wrapper {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 45px);
  min-height: calc(100 * var(--vh, 1vh) - 45px);
  padding-top: 55px;

  background-color: $color-default-white;
  border-top-left-radius: 10px;
  box-shadow: $box-shadow-purple-basic;

  @include viewport--sm {
    padding-top: 0;
  }
}

.section-wrapper-title {
  display: flex;
  align-items: center;

  h1 {
    margin: 0;
    padding: 0;

    font-size: 32px;
    line-height: 41px;
    font-weight: normal;

    @include viewport--sm {
      font-size: 22px;
      line-height: 28px;
    }
  }

  a {
    margin-left: 9px;

    @include viewport--sm {
      margin-left: 4px;
    }
  }
}

.margin {
  margin-bottom: 60px;

  @include viewport--sm {
    margin-bottom: 40px;
  }
}

.choices {
  max-width: 366px;
  margin-bottom: 10px;
  z-index: 100;

  &:last-child {
    margin-bottom: 10px;
  }
}

.choices.is-open {
  z-index: 101;
}

.choices__inner {
  max-width: 366px;
  width: 100%;
  height: 60px;
  padding: 14.2px 27px 22.9px 16px;

  &::after {
    content: "";
    position: absolute;
    top: 24.5px;
    right: 15px;
    width: 6px;
    height: 11px;
    background-image: url("../img/svg/arrow.svg");
    transform: rotate(90deg);
  }
}

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}
