.container {
  max-width: calc(894px + 16.6%);
  padding: 0 8.3%;

  &--support {
    max-width: calc(690px + 16.6%);
  }

  @include viewport--lg {
    padding: 0 101px;

    &--works {
      max-width: calc((100vw - 425px) * 0.882 + 202px);
    }

    &--support {
      width: calc(100vw - 221px);
      max-width: 892px;
    }
  }

  @include viewport--md {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0 8.3%;

    &--works,
    &--support {
      max-width: 100%;
    }
  }

  @include viewport--sm {
    padding: 0 5.33vw;
  }

  &--no-padding {
    @include viewport--sm {
      padding: 0;
    }
  }
}
