.header {
  width: 221px;
  height: 100vh;
  height: calc(100 * var(--vh, 1vh));
  min-height: max-content;
  padding: 27px 43px 27px 20px;

  @include viewport--md {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;

    width: 100%;
    padding: 0;

    overflow-y: auto;
    transition: background $default-transition-settings;

    &--closed {
      display: none;
    }

    &--opened {
      background-color: $color-alice-blue;

      .header__wrapper {
        transform: none;
      }
    }
  }

  nav {
    margin-top: 36px;
  }
}

.header__menu-close {
  display: none;

  color: $color-rum;

  @include viewport--md {
    display: initial;
    height: 25px;

    &:focus,
    &:hover {
      color: $color-rich-blue;
    }

    &:active {
      color: $color-selago-1;
    }

    svg {
      padding: 6px;
    }
  }
}

.header__wrapper {
  display: flex;
  flex-direction: column;

  @include viewport--md {
    flex-direction: row;
    flex-wrap: wrap;
    align-content: flex-start;
    align-items: center;
    justify-content: space-between;
    width: 233px;
    min-height: 100vh;
    min-height: calc(100 * var(--vh, 1vh));
    padding: 55px 19px 33px 19px;

    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: $color-default-white;
    box-shadow: $box-shadow-mobile-menu;

    transform: translateX(-110%);
    transition: transform $default-transition-settings;

    .header__logo {
      margin-left: 15px;
    }

    li {
      width: 158px;
    }
  }
}

.header__logo {
  align-self: center;
  margin-left: 6px;
}
