.statistics__overall-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 48px;
  padding: 0;

  list-style: none;

  li {
    display: flex;
    align-items: center;
    flex-basis: 32%;
  }

  p {
    display: flex;
    flex-wrap: wrap;
    max-width: 135px;
  }

  @include viewport--sm {
    flex-direction: column;
    margin-top: 11px;
    margin-bottom: 38px;

    li {
      margin-bottom: 10px;
    }

    p {
      margin: 0;
      max-width: 100%;
      margin-bottom: 3px;
    }
  }
}

.statistics__overall-count {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  min-width: 75px;
  height: 102px;
  margin-right: 20px;
  padding: 0 10px;

  font-size: 22px;
  line-height: 28px;

  border: 1px solid $color-selago-1;
  border-radius: 44px;

  @include viewport--sm {
    flex-direction: row;
    width: 90px;
    min-width: max-content;
    height: 55px;

    font-size: 16px;
    line-height: 21px;

    svg {
      margin-right: 4px;
    }
  }
}
