.chart__wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 322px;
  padding-right: 7px;
  margin-top: 32px;

  img {
    max-height: 100%;
    object-fit: contain;
  }

  @include viewport--sm {
    flex-direction: column;
    height: auto;
    margin-top: 0;
    padding-right: 0;
  }
}

.chart__wrapper > *:first-child {
  width: 63.7%;
  flex-basis: 100%;
  max-height: 100%;

  @include viewport--sm {
    width: 100%;
  }
}

.chart__period {
  position: relative;

  align-self: flex-end;
  width: 31.6%;
  margin: 6px 0 20px;

  label {
    display: none;
  }

  @include viewport--sm {
    order: -1;

    width: 100%;
  }
}

.chart__legend {
  align-self: flex-end;
  width: 31.6%;

  @include viewport--sm {
    width: 100%;
    max-width: 571px;
    margin-top: 28px;
    margin-right: auto;
  }
}

.chart__legend-list {
  padding: 0;

  font-size: 14px;
  line-height: 18px;
}

.chart__legend-item {
  display: flex;
  align-items: center;
  margin-bottom: 14px;

  color: $color-rum;

  &::before {
    content: "";
    width: 8px;
    height: 8px;
    margin-right: 13px;

    border-radius: 50%;
  }

  &--1 {
    &::before {
      background-color: $eucalyptus;
    }
  }

  &--2 {
    &::before {
      background-color: $dodger-blue;
    }
  }

  &--3 {
    &::before {
      background-color: $deep-cerise;
    }
  }

  span {
    min-width: 30px;
    margin-left: auto;

    text-align: left;
  }
}
