.questions {
  list-style: none;
  padding: 0;
  margin-top: 50px;

  time {
    flex-basis: 80%;
    color: $color-rum;
    font-size: 12px;
    line-height: 15px;
  }

  p {
    margin-top: 0;
  }

  @include viewport--sm {
    margin-top: 29px;
  }

  li {
    position: relative;
  }
}

.questions__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;

  background-color: $color-snow;
  border-radius: 10px;

  overflow: hidden;

  &--answered {
    background-color: $color-lavender;

    @include viewport--sm {
      margin-bottom: 22px;
    }
  }

  &--closed {
    background-color: $color-snow;

    @include viewport--sm {
      margin-bottom: 11px;
    }

    .questions__text {
      background-color: $color-snow;
    }

    .questions__answer-wrapper {
      margin-top: -250%;
    }

    // .questions__answer {
    //   height: 0;
    // }
  }
}

.questions__text {
  position: relative;
  z-index: 2;

  padding: 21px 110px 21px 32px;
  border-radius: 10px;

  p {
    margin-bottom: 9px;
  }

  &::after {
    display: none;
    position: absolute;
    right: 28px;
    top: 50%;

    align-items: center;
    justify-content: center;
    min-width: 30px;
    height: 30px;

    color: $color-default-white;

    background: $color-rich-blue;
    border-radius: 10px;
    transform: translateY(-50%);

    @include viewport--sm {
      right: 10px;
    }
  }

  @include viewport--sm {
    padding-right: 60px;
    padding-left: 21px;
  }

  &--answered {
    background-color: $color-lavender;
    cursor: pointer;

    &:hover,
    &:focus {
      color: $color-rum;
      background-color: $color-lavender;
    }
  }

  &--unread {
    &::after {
      content: "1";
      display: flex;
    }
  }
}

.questions__answer {
  height: max-content;

  background-color: $color-lavender;
  border-radius: 10px;
  transition: all $default-transition-settings;
}

.questions__answer-wrapper {
  height: 100%;
  display: flex;
  align-items: flex-start;
  padding: 21px 76px 21px 23px;
  margin: 6px 30px 30px;

  background-color: $color-default-white;
  box-shadow: $box-shadow-dropdown-shadow;
  border-radius: 10px;
  transition: all $default-transition-settings;

  p {
    margin-bottom: 11px;

    @include viewport--sm {
      margin-bottom: 9px;
    }
  }

  @include viewport--sm {
    margin: 2px 20px 20px;
    padding-right: 13px;
    padding-left: 21px;
  }
}

.questions__answer-avatar {
  flex-shrink: 0;
  margin-top: 2px;
  margin-right: 24px;

  img {
    width: 30px;
    height: 30px;

    object-fit: cover;
    border-radius: 50%;
  }

  @include viewport--sm {
    margin-right: 12px;
  }
}

.questions__tooltip-btn {
  position: absolute;
  right: 34px;
  top: 50%;
  z-index: 2;

  display: flex;
  width: 17px;
  height: 17px;

  transform: translateY(-50%);

  @include viewport--sm {
    right: 16px;
  }

  button {
    width: 100%;

    background: url("../img/svg/tooltip.svg") center center no-repeat;
    border: none;
  }
}
