.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  &--profile {
    align-items: flex-start;

    @include viewport--lg {
      width: 100%;
    }
  }
}

.form__item {
  position: relative;
  width: 100%;
  min-height: 60px;
  margin-bottom: 20px;

  &--sm {
    margin-bottom: 10px;
  }

  &--forgot-password {
    margin-bottom: 0;
  }

  input,
  textarea {
    display: block;
    max-width: 386px;
    width: 100%;
    height: 60px;
    padding: 19.2px 21px 2px 22px;

    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    border: 1px solid $color-selago-1;
    border-radius: 10px;
    transition: border $default-transition-settings;

    &::placeholder {
      color: $color-rum;
    }

    &:hover,
    &:focus,
    &:active {
      border: 1px solid $color-rich-blue;
    }

    &:disabled {
      background-color: $color-ghost-white;
      pointer-events: none;
    }
  }

  textarea {
    height: auto;
  }

  input.on-error,
  textarea.on-error {
    border: 1px solid $color-flamingo;

    &:hover,
    &:focus,
    &:active {
      border: 1px solid $color-flamingo;
    }
  }

  input.on-valid,
  textarea.on-valid {
    border: 1px solid $color-selago-1;

    &:hover,
    &:focus,
    &:active {
      border: 1px solid $color-rich-blue;
    }
  }

  label {
    position: absolute;
    top: 20px;
    left: 21px;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;

    transition: top $default-transition-settings,
      font-size $default-transition-settings,
      line-height $default-transition-settings;
    cursor: pointer;
    z-index: 2;
  }

  &--fix {
    @include viewport--sm {
      input {
        max-width: 100%;
      }
    }

    label {
      top: 8px;

      font-size: 12px;
      line-height: 15px;
      pointer-events: none;
    }
  }

  &--big {
    margin-bottom: 10px;

    @include viewport--sm {
      input {
        max-width: 100%;
      }
    }
  }

  &--light {
    margin-top: 10px;
    margin-bottom: 10px;

    input {
      font-weight: 400;
    }
  }

  .form__icon-password {
    position: absolute;
    top: 12.5px;
    right: 3px;

    width: 40px;
    height: 40px;
    cursor: pointer;
    opacity: 1;
    transition: opacity $default-transition-settings;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.5;
    }
  }

  span {
    display: flex;
    margin-top: 10px;
    font-style: italic;
    font-size: 12px;
    line-height: 16px;
    color: $color-flamingo;
  }
}

.form__item input:focus + label,
.form__item input:valid + label {
  top: 8px;
  font-size: 12px;
  line-height: 15px;
}

.form__picture {
  position: relative;
  max-width: 366px;
  width: 100%;
  height: 328px;

  @include viewport--av {
    max-width: 100%;
    margin-bottom: 19px;
  }

  label {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 100%;
    height: 328px;
    max-height: 100%;

    background-color: $color-default-white;
    border-radius: 10px;
    transition: background-color $default-transition-settings;
    cursor: pointer;
    overflow: hidden;

    background-image: repeating-linear-gradient(20deg, $color-selago-1, $color-selago-1 15px, transparent 15px, transparent 31px, $color-selago-1 31px),
      repeating-linear-gradient(110deg, $color-selago-1, $color-selago-1 15px, transparent 15px, transparent 31px, $color-selago-1 31px),
      repeating-linear-gradient(200deg, $color-selago-1, $color-selago-1 15px, transparent 15px, transparent 31px, $color-selago-1 31px),
      repeating-linear-gradient(290deg, $color-selago-1, $color-selago-1 15px, transparent 15px, transparent 31px, $color-selago-1 31px);
    background-size: 1px 100%, 100% 1px, 1px 100%, 100% 1px;
    background-position: 0 0, 0 0, 100% 0, 0 100%;
    background-repeat: no-repeat;

    &:hover,
    &:focus,
    &:active {
      background-color: $color-selago-2;
    }

    @include viewport--lg {
      margin-left: 10px;
    }

    @include viewport--av {
      max-width: 100%;
      height: 328px;
      order: 1;
      margin: 0;
      margin-bottom: 19px;
    }
  }

  .form__upload-img {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;

    border-radius: 10px;

    &--rounded {
      margin: 0;
      min-width: 0;
      min-height: 0;
      width: 180px;
      height: 180px;
      border-radius: 50%;
    }
  }

  .form__picture-icon {
    margin-top: 14px;
    margin-bottom: 20px;

    &--upload {
      width: 40px;
      height: 40px;

      border-radius: 50%;

      label {
        margin: 0;

        background-image: none;
        border-radius: 50%;
      }
    }

    &--upload-dark {
      width: 30px;
      height: 30px;

      border-radius: 50%;
      background-color: $color-night-rider;

      label {
        margin: 0;

        background-image: none;
        background-color: $color-night-rider;
        border-radius: 50%;

        &:hover,
        &:focus,
        &:active {
          background-color: $color-rum;
        }
      }
    }
  }

  input[type="file"] {
    outline: 0;
    opacity: 0;
    pointer-events: none;
    user-select: none;
    visibility: hidden;
    height: 0;
    width: 0;
  }

  span {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: $color-blackcurrant;
  }

  .form__picture-text {
    max-width: 228px;
    margin-bottom: 15px;

    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    text-align: center;

    &--lg {
      @include viewport--av {
        display: none;
      }
    }

    &--sm {
      display: none;

      @include viewport--av {
        display: block;
      }
    }
  }
}

.form__picture-icon-box {
  position: absolute;
  top: 20px;
  right: 21px;
  display: flex;
  flex-direction: column;

  .form__picture-icon {
    margin-top: 0;
    margin-bottom: 9px;
  }

  &--dark {
    position: absolute;
    left: 21px;

    margin-left: -10px;
    margin-top: -10px;

    button {
      flex-basis: auto;
    }


    .form__picture-icon {
      margin-top: 0;
      margin-bottom: 5px;
    }
  }
}
