.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: $color-modal-overlay;
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include viewport--sm {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 588px;
    padding: 101px;

    background-color: $color-default-white;
    box-shadow: $box-shadow-dropdown-shadow;
    border-radius: 10px;

    @include viewport--md {
      width: 100%;
      padding: 57px 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 28px;
    right: 27px;
    color: $color-rum;

    @include viewport--sm {
      top: 16px;
      right: 18.5px;
    }

    &:hover,
    &:focus {
      color: $color-rich-blue;

      &::before {
        // box-shadow: 0 0 5px 1px $color-prelude;
        background-color: transparentize($color-rum, 0.95);
        border-radius: 10px;
      }
    }

    // увеличивает кликабельную область
    &::before {
      position: absolute;
      top: -13px;
      right: -15px;
      content: "";
      width: 40px;
      height: 40px;
      background-color: transparent;
      cursor: pointer;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include viewport--sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  .choices {
    max-width: 386px;
    margin: 0;
  }

  .choices__inner {
    max-width: 386px;
    margin: 0;
  }
}

.modal__select-box {
  max-width: 386px;
  width: 100%;
  margin-bottom: 40px;

  @include viewport--sm {
    margin-bottom: 20px;
  }
}

.modal__inner-wrapper {
  display: flex;
  width: 100%;

  @include viewport--md {
    flex-direction: column;

    .modal__select-box {
      order: 1;
    }
  }
}

.modal__btn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  max-width: 386px;
  width: 100%;

  button {
    flex-basis: 183px;

    @include viewport--md {
      flex-basis: 140px;
    }
  }

  .modal__close-btn {
    position: initial;

    &::before {
      content: none;
    }

    &--white {
      color: $color-default-white;
    }

    &--width {
      flex-basis: 100%;
    }
  }
}


.modal--delete-profile,
.modal--work,
.modal--question {
  p:first-child {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 23px;
  }

  p {
    max-width: 314px;
    margin: 0;
    margin-bottom: 41px;
    padding: 0;
    font-size: 14px;
    line-height: 18px;
    text-align: center;

    @include viewport--sm {
      max-width: 295px;
      margin-bottom: 30px;
    }
  }
}

.modal--work {
  .modal__content {
    width: 994px;
    max-width: 100%;

    @include viewport--md {
      width: 335px;
      padding-top: 30px;
      padding-bottom: 20px;
    }
  }

  .modal__select-box {
    margin-right: 20px;
    margin-bottom: 0;
  }

  .modal__wrapper {
    padding-top: 29px;
    padding-bottom: 20px;
  }

  p {
    margin-bottom: 45px;

    @include viewport--md {
      max-width: 254px;
      margin-bottom: 25px;
    }
  }

  p:first-child {
    margin-bottom: 39px;

    @include viewport--md {
      margin-bottom: 20px;
    }
  }

  .choices {
    margin-bottom: 20px;
  }

  .choices__list {
    @include viewport--md {
      padding-top: 1px;
    }
  }

  .form__item {
    margin-bottom: 41px;

    @include viewport--md {
      margin-bottom: 20px;
    }
  }

  .form__picture {
    width: 386px;
    max-width: 100%;
    height: 300px;

    @include viewport--md {
      height: 200px;
      margin-bottom: 21px;
    }

    label {
      @include viewport--md {
        margin-left: 0;
      }
    }
  }
}

.modal--edit-work {
  p:first-child {
    margin-bottom: 20px;
  }

  .modal__card-wrapper {
    height: 100%;
    max-height: 100%;
  }

  .form__picture {
    @include viewport--md {
      height: 239px;
    }
  }
}

.modal--message {
  .modal__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 588px;

    text-align: center;

    @include viewport--md {
      width: 335px;
      padding-top: 63px;
      padding-bottom: 63px;
    }
  }

  p {
    margin-top: 0;
    margin-bottom: 0;

    &:not(:first-of-type) {
      max-width: 199px;
    }
  }

  p:first-of-type {
    margin-bottom: 23px;

    font-size: 18px;
    line-height: 23px;
  }

  .modal__btn {
    margin-top: 26px;

    @include viewport--md {
      margin-top: 5px;
    }
  }
}

.modal--add-work-success {
  .modal__btn {
    margin-top: 30px;
  }

  button:first-child {
    margin-right: 0;
  }
}

.modal--delete-work {
  p:first-of-type {
    @include viewport--sm {
      max-width: 200px;
    }
  }
}

.modal--question {
  p:first-of-type {
    margin-bottom: 38px;

    @include viewport--sm {
      margin-bottom: 31px;
    }
  }

  textarea {
    min-height: 110px;
    margin-top: 20px;
    margin-bottom: 41px;

    @include viewport--sm {
      margin-bottom: 31px;
    }
  }

  .modal__select-box {
    margin-bottom: 0;
  }

  .modal__content {
    @include viewport--sm {
      width: 335px;
      padding-top: 44px;
      padding-bottom: 44px;
    }
  }
}
