.card-work--new {
  text-align: center;

  .card-work__picture {
    top: 15%;
    right: 0;

    width: 23.86%;
    height: 28.33%;
    margin: 0 auto;

    box-shadow: $box-shadow-new-work;

    @include viewport--sm {
      width: 19.86%;
    }
  }

  b {
    margin-top: auto;
    padding-top: 8%;

    font-size: 16px;
    line-height: 21px;
    font-weight: normal;

    @include viewport--sm {
      padding-top: 5%;
    }
  }

  p {
    max-width: 194px;
    margin: 2% auto auto;

    color: $color-rum;
  }
}

.card-work__wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  height: calc((100vw - 425px) * 0.236);
  max-height: 240px;
  min-height: 183px;
  padding: 12px;

  border-radius: 10px;
  background-color: $color-lavender;

  button {
    position: relative;
    z-index: 1;

    flex-shrink: 0;

    &:not(:last-of-type) {
      margin-bottom: 5px;
    }
  }

  @include viewport--md {
    height: 22.15vw;
    max-height: 100%;
  }

  @include viewport--sm {
    height: 64vw;
    padding: 10px;
  }
}

.card-work__picture {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;

  display: flex;
  height: 100%;
  width: 100%;

  border-radius: 10px;
  overflow: hidden;

  a {
    display: flex;
    height: 100%;

    @include viewport--sm {
      width: 100%;
    }
  }

  img {
    object-fit: cover;
    min-height: 100%;
    min-width: 100%;
  }

  picture {
    @include viewport--sm {
      width: 100%;
    }
  }
}

.card-work__info {
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin: 15px 10px 16px;

  font-size: 12px;
  line-height: 15px;

  time {
    margin-left: auto;

    color: $color-rum;
  }

  @include viewport--sm {
    margin-top: 14px;
  }
}

.card-work__stats {
  display: flex;
  align-items: center;

  margin-right: 13px;

  svg {
    margin-right: 3px;
  }
}

.card-work__tag-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 17px;

  list-style: none;

  li {
    height: 31px;
    max-width: 100%;
    width: max-content;
    flex-shrink: 0;
    margin-bottom: 3px;
    margin-right: 5px;

    color: $color-rum;
  }

  p {
    position: relative;

    padding: 6px 11px;
    margin: 0;

    border: 1px solid $color-selago-1;
    border-radius: 10px;
    background-color: $color-default-white;

    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis;

    &:hover {
      z-index: 1;

      width: 100%;

      white-space: normal;
    }
  }
}
