.works__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 30px;
  margin-right: -21px;
  margin-bottom: 0;
  padding: 0;

  list-style: none;

  @include viewport--lg {
    margin-right: calc((100vw - 425px) * (-0.025));
  }

  @include viewport--md {
    margin-right: -1.5vw;
  }

  @include viewport--sm {
    flex-direction: column;
    margin-right: 0;
  }
}

.works__card {
  flex-basis: calc((100vw - 425px) * 0.281);
  max-width: 285px;
  min-width: 205px;
  margin-right: 20px;
  margin-bottom: 59px;

  @include viewport--lg {
    margin-right: calc((100vw - 425px) * 0.02);
  }

  @include viewport--md {
    flex-basis: 26.8vw;
    margin-right: 1.5vw;
  }

  @include viewport--sm {
    flex-basis: auto;
    width: 100%;
    min-width: 100%;
    margin-right: 0;
    margin-bottom: 36px;
  }
}
