.premium {
  position: relative;

  min-height: 472px;
  max-width: 766px;
  width: 65%;
  padding-top: 78px;
  margin-right: auto;
  padding-bottom: 78px;

  a {
    max-width: 100%;
  }

  @include viewport--md {
    margin-left: 0;
  }

  @include viewport--sm {
    width: 100%;
    padding-top: 45px;
    padding-right: 20px;
    padding-bottom: 59px;
  }
}

.premium__promo {
  max-width: 480px;
  margin-bottom: 35px;
  padding-left: 4px;

  font-size: 32px;
  line-height: 41px;
  font-weight: normal;
  color: $color-default-white;

  img {
    min-height: 100%;

    border-radius: 50%;
    box-shadow: $box-shadow-shadow-button;

    object-fit: cover;
  }

  @include viewport--sm {
    width: 51%;
    padding-left: 0;

    font-size: 22px;
    line-height: 32px;
  }

  span {
    display: inline-block;
  }
}

.premium__inner {
  display: inline-block;
}

.premium__avatar {
  position: relative;

  display: inline-block;
  margin: 0 6px 0 2px;
  vertical-align: middle;
  width: 60px;
  height: 60px;

  @include viewport--sm {
    width: 39px;
    height: 39px;
    margin-bottom: -5px;
    margin-top: -5px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 3px;
    right: -3px;

    display: inline-block;
    width: 20px;
    height: 20px;

    background: url("../img/svg/premium-user.svg") center center no-repeat;

    @include viewport--sm {
      right: -1px;
      width: 12px;
      height: 12px;
    }
  }
}

.premium__features-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: 5px;
  margin-bottom: 22px;
  padding: 0;

  color: transparentize($color-default-white, 0.3);
  list-style: none;

  li {
    flex-basis: 151px;
    margin-bottom: 20px;

    &:nth-of-type(2n) {
      flex-basis: 168px;
    }

    &:not(:last-of-type) {
      margin-right: 44px;
    }
  }

  @include viewport--md {
    flex-direction: column;
    max-width: 100%;
    margin-top: 46px;
    margin-bottom: 25px;

    li {
      position: relative;

      flex-basis: 100%;
      padding-left: 25px;
      margin-bottom: 21px;

      &:nth-of-type(2n) {
        flex-basis: 100%;
      }

      &::before {
        content: "";
        position: absolute;
        left: -5px;
        top: 9px;

        display: block;
        width: 20px;
        height: 1px;

        background-color: $color-default-white;
      }
    }
  }
}

.premium__picture {
  position: absolute;
  top: 50%;
  right: -46%;

  width: 43%;
  max-height: 90%;
  transform: translateY(-53%);

  img {
    width: 100%;

    object-fit: contain;
    object-position: center;
  }

  @include viewport--sm {
    top: 0;
    left: auto;
    right: 3%;

    display: flex;
    width: 175px;
    height: 43%;
    transform: none;
  }
}
