.no-questions {
  display: flex;
  width: 894px;
  max-width: calc((100vw - 425px) * 0.69 + 202px);

  background: $color-snow;
  background-image: repeating-linear-gradient(20deg, $color-prelude, $color-prelude 15px, transparent 15px, transparent 31px, $color-prelude 31px), repeating-linear-gradient(110deg, $color-prelude, $color-prelude 15px, transparent 15px, transparent 31px, $color-prelude 31px), repeating-linear-gradient(200deg, $color-prelude, $color-prelude 15px, transparent 15px, transparent 31px, $color-prelude 31px), repeating-linear-gradient(290deg, $color-prelude, $color-prelude 15px, transparent 15px, transparent 31px, $color-prelude 31px);
  background-size: 1px 100%, 100% 1px, 1px 100%, 100% 1px;
  background-position: 0 0, 0 0, 100% 0, 0 100%;
  background-repeat: no-repeat;
  border-radius: 10px;

  @include viewport--md {
    width: 100%;
    max-width: 100%;
  }

  @include viewport--sm {
    height: 85.92vh;
    height: calc(85.92 * var(--vh, 1vh));
  }
}

.no-questions__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 414px;
  min-height: 455px;
  margin: auto;
  padding: 50px 20px;

  text-align: center;

  p:first-of-type {
    margin-bottom: 6px;
    margin-top: 0;

    font-size: 18px;
    line-height: 23px;

    @include viewport--sm {
      margin-bottom: 3px;
    }
  }

  p:not(:first-of-type) {
    color: $color-rum;
    width: 335px;
  }

  a {
    margin-top: 65px;
    width: 89%;

    @include viewport--sm {
      width: 100%;
      margin-top: 44px;
      margin-bottom: 4px;
    }
  }

  @include viewport--sm {
    width: 100%;
    min-width: 0;
  }
}
