/*===============================
=            Choices            =
===============================*/

$choices-selector: "choices" !default;
$choices-font-size-lg: 16px !default;
$choices-font-size-md: 14px !default;
$choices-font-size-sm: 12px !default;
$choices-guttering: 24px !default;
$choices-border-radius: 10px !default;
$choices-border-radius-item: 10px !default;
$choices-bg-color: #ffffff !default;
$choices-bg-color-disabled: #f2f2f4 !default;
$choices-bg-color-dropdown: #ffffff !default;
$choices-text-color: #333333 !default;
$choices-keyline-color: #e8deeb !default;
$choices-primary-color: #00bcd4 !default;
$choices-disabled-color: #6b5b73 !default;
$choices-label-color: #6b5b73 !default;
$choices-highlight-color: $choices-primary-color !default;
$choices-button-dimension: 8px !default;
$choices-button-offset: 8px !default;
$box-shadow-dropdown-shadow: 0 7px 20px rgba(82, 72, 191, 0.07);
$choice-icon-arrow: url("../img/svg/arrow.svg");
$choices-icon-cross: url(data:image/svg+xml;base64,) !default;
$choices-icon-cross-inverse: url(data:image/svg+xml;base64,) !default;
$choices-z-index: 0;
$choices-wrapper-z-index: 1;
$choices-label-z-index: 2;

.#{$choices-selector} {
  position: relative;
  overflow: hidden;
  margin-bottom: $choices-guttering;
  font-size: $choices-font-size-lg;
  width: 100%;

  &:focus {
    outline: none;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &.is-open {
    overflow: initial;
  }

  &.is-disabled {
    pointer-events: none;

    .#{$choices-selector}__inner,
    .#{$choices-selector}__input {
      background-color: $choices-bg-color-disabled;
      cursor: not-allowed;
      user-select: none;
    }

    .#{$choices-selector}__item {
      cursor: not-allowed;
    }
  }

  [hidden] {
    display: none !important;
  }

  .#{$choices-selector}__inner {
    display: flex;
    align-items: center;

    &::after {
      content: "";
      align-self: center;
      display: block;
      width: 6px;
      height: 11px;
      background: $choice-icon-arrow center center no-repeat;
    }
  }

  &--labeled {
    margin-bottom: 0;

    & + label {
      position: absolute;
      left: 21px;
      top: 50%;
      color: $choices-label-color;
      transform: translateY(-50%);
      z-index: $choices-label-z-index;
    }
  }

  &--selected {
    .#{$choices-selector}__inner {
      align-items: flex-end;
    }

    & + label {
      top: 10px;
      transform: none;

      font-size: 12px;
      line-height: 15px;
    }
  }
}

.#{$choices-selector}[data-type*="select-one"],
.#{$choices-selector}[data-type*="select-multiple"] {
  .#{$choices-selector}__inner {
    padding-bottom: 12.5px;
  }
}
.#{$choices-selector}[data-type*="select-one"] {
  cursor: pointer;

  .#{$choices-selector}__input {
    display: block;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid $choices-keyline-color;
    background-color: #ffffff;
    margin: 0;
  }

  .#{$choices-selector}__button {
    background-image: $choices-icon-cross-inverse;
    padding: 0;
    background-size: 8px;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -10px;
    margin-right: 25px;
    height: 20px;
    width: 20px;
    border-radius: 10em;
    opacity: 0.25;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  .#{$choices-selector}__item[data-value=""] .#{$choices-selector}__button {
    display: none;
  }

  &.is-open::after {
    border-color: transparent transparent $choices-text-color transparent;
    margin-top: -7.5px;
  }

  &[dir="rtl"] {
    &::after {
      left: 11.5px;
      right: auto;
    }

    .#{$choices-selector}__button {
      right: auto;
      left: 0;
      margin-left: 25px;
      margin-right: 0;
    }
  }
}

.#{$choices-selector}[data-type*="select-multiple"],
.#{$choices-selector}[data-type*="text"] {
  .#{$choices-selector}__inner {
    cursor: text;
  }

  .#{$choices-selector}__button {
    position: relative;
    display: inline-block;
    margin-top: 0;
    margin-right: -$choices-button-offset/2;
    margin-bottom: 0;
    margin-left: $choices-button-offset;
    padding-left: $choices-button-offset * 2;
    border-left: 1px solid darken($choices-primary-color, 10%);
    background-image: $choices-icon-cross;
    background-size: $choices-button-dimension;
    width: $choices-button-dimension;
    line-height: 1;
    opacity: 0.75;
    border-radius: 0;

    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.#{$choices-selector}__inner {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  background-color: $choices-bg-color;
  padding: 7.5px 16.5px 3.75px;
  border: 1px solid $choices-keyline-color;
  border-radius: $choices-border-radius;
  font-size: $choices-font-size-md;
  min-height: 60px;
  overflow: hidden;
  transition: border-color 0.3s ease;

  .is-focused &,
  .is-open &,
  &:hover {
    border-color: #660000;
  }

  .is-open & {
    position: relative;
    box-shadow: $box-shadow-dropdown-shadow;
    z-index: $choices-wrapper-z-index;
  }

  .is-flipped.is-open & {
    border-radius: 0 0 $choices-border-radius $choices-border-radius;
  }
}

.#{$choices-selector}__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.#{$choices-selector}__list--single {
  display: inline-block;
  padding: 4px 16px 4px 4px;
  width: 100%;

  [dir="rtl"] & {
    padding-right: 4px;
    padding-left: 16px;
  }

  .#{$choices-selector}__item {
    width: 100%;
  }
}

.#{$choices-selector}__list--multiple {
  display: inline;

  .#{$choices-selector}__item {
    display: inline-block;
    vertical-align: middle;
    border-radius: $choices-border-radius-item;
    padding: 4px 10px 4px 4px;
    font-size: 14px;
    font-weight: 400;
    margin-right: 3.75px;
    margin-top: 1.5px;
    margin-bottom: 1.5px;
    background-color: #ffffff;
    border: 1px solid #ffffff;
    // color: #ffffff;
    word-break: break-all;
    box-sizing: border-box;

    &[data-deletable] {
      padding-right: 5px;
    }

    [dir="rtl"] & {
      margin-right: 0;
      margin-left: 3.75px;
    }

    &.is-highlighted {
      background-color: darken($choices-primary-color, 5%);
      border: 1px solid darken($choices-primary-color, 10%);
    }

    .is-disabled & {
      background-color: darken($choices-disabled-color, 25%);
      border: 1px solid darken($choices-disabled-color, 35%);
    }
  }
}

.#{$choices-selector}__list--dropdown {
  visibility: hidden;
  z-index: $choices-z-index;
  position: absolute;
  width: 100%;
  max-height: 252px;
  background-color: $choices-bg-color-dropdown;
  border: 1px solid $choices-keyline-color;
  top: 100%;
  margin-top: -1px;
  border-bottom-left-radius: $choices-border-radius;
  border-bottom-right-radius: $choices-border-radius;
  overflow-y: scroll;
  overflow-x: hidden;
  word-break: break-all;
  will-change: visibility;
  margin-top: -6px;
  padding-top: 10px;

  &.is-active {
    visibility: visible;
  }

  .is-flipped & {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: -1px;
    border-radius: 0.25rem 0.25rem 0 0;
  }

  .#{$choices-selector}__list {
    position: relative;
    max-height: 300px;
    //- c overflow: hidden; не работает прокрутка после 7-ого пункта option
    -webkit-overflow-scrolling: touch;
    will-change: scroll-position;
  }

  .#{$choices-selector}__item {
    position: relative;
    padding: 10px;
    font-size: $choices-font-size-md;

    [dir="rtl"] & {
      text-align: right;
    }
  }

  .#{$choices-selector}__item--selectable {
    @media (min-width: 640px) {
      padding-right: 100px;

      &:after {
        content: attr(data-select-text);
        font-size: $choices-font-size-sm;
        opacity: 0;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
      }

      [dir="rtl"] & {
        text-align: right;
        padding-left: 100px;
        padding-right: 10px;

        &:after {
          right: auto;
          left: 10px;
        }
      }
    }

    &.is-highlighted {
      background-color: mix(#000000, #ffffff, 5%);

      &:after {
        //opacity: 0.5;
      }
    }
  }
}

.#{$choices-selector}__item {
  cursor: default;
}

.#{$choices-selector}__item--selectable {
  cursor: pointer;
}

.#{$choices-selector}__item--disabled {
  cursor: not-allowed;
  user-select: none;
  opacity: 0.5;
}

.#{$choices-selector}__heading {
  font-weight: 600;
  font-size: $choices-font-size-sm;
  padding: 10px;
  border-bottom: 1px solid lighten($choices-keyline-color, 10%);
  color: lighten(#333, 30%);
}

.#{$choices-selector}__button {
  text-indent: -9999px;
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.#{$choices-selector}__input {
  display: inline-block;
  vertical-align: baseline;
  background-color: $choices-bg-color;
  font-size: $choices-font-size-md;
  border: 0;
  border-radius: 0;
  max-width: 100%;
  padding: 4px 0 4px 2px;

  &:focus {
    outline: 0;
  }

  [dir="rtl"] & {
    padding-right: 2px;
    padding-left: 0;
  }
}

.#{$choices-selector}__placeholder {
  opacity: 0.5;
}

/*=====  End of Choices  ======*/
