.login {
  position: relative;
  padding-top: 55px;
  padding-bottom: 30px;

  @include viewport--md {
    padding-top: 40px;
  }
}

.login__content {
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 336px;
  min-width: 336px;
  min-height: 600px;
  margin: 0 auto;

  @include viewport--lg {
    max-width: 27%;
  }

  @include viewport--xs {
    min-width: 300px;
  }
}

.login__form-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.login__logo {
  width: 82.71px;
  height: 68px;
  margin-bottom: 58px;

  &--forgot-password {
    margin-bottom: 88px;
  }

  &--forgot-password-success {
    margin-bottom: 182px;
  }

  &--success {
    margin-bottom: 182px;
  }

  &--signup,
  &--req {
    margin-bottom: 33px;
  }

  &--req-success {
    margin-bottom: 153px;
  }
}

.login__title {
  margin: 0;
  padding: 0;

  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
  color: $color-blackcurrant;
  text-align: center;

  &--req-success {
    max-width: 300px;
  }
}

.login__info {
  max-width: 319px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 0;
  text-align: center;

  font-size: 14px;
  line-height: 18px;
  color: $color-rum;

  &--signup,
  &--req {
    max-width: 350px;
    margin-top: 20px;

    @include viewport--md {
      max-width: 330px;
    }
  }

  &--forgot-password {
    margin-top: 20px;
  }

  &--forgot-password-success {
    margin-top: 20px;
  }

  &--success {
    @include viewport--md {
      margin-top: 20px;
    }
  }

  &--req-success {
    margin-top: 19px;
  }
}

.login__form {
  a {
    font-size: 14px;
    line-height: 18px;
    color: $color-rum;
    transition: color $default-transition-settings;

    &:hover,
    &:focus,
    &:active {
      color: $color-blackcurrant;
    }
  }

  button {
    margin-top: 40px;
  }

  &--forgot-password {
    button:last-of-type {
      margin-top: 10px;
    }
  }

  &--signup,
  &--req {
    button {
      margin-top: 20px;
    }
  }

  &--req-success {
    button {
      margin-top: -20px;
    }
  }
}

.login__inst {
  margin-top: 10px;

  svg {
    margin-right: 9px;
  }
}

.login__link-wrap {
  margin-top: 40px;

  @include viewport--av {
    margin-top: 20px;
  }
}

.login__link {
  display: flex;
  max-width: 300px;
  margin: 0 auto;
  padding: 0;

  font-size: 14px;
  line-height: 18px;
  color: $color-rum;
  transition: color $default-transition-settings;

  &:hover,
  &:focus,
  &:active {
    color: $color-blackcurrant;
  }
}
