.profile {
  padding-bottom: 40px;

  h2 {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: $color-blackcurrant;

    @include viewport--sm {
      padding: 0 5.33vw;
    }
  }

  .form__item .form__label {
    top: 30px;

    @include viewport--xs {
      left: 40px;
    }
  }
}

.profile__header {
  display: flex;
  flex-direction: row;

  h1 {
    margin-right: 13px;

    @include viewport--sm {
      margin-right: 5px;
    }
  }

  img {
    height: 43px;
    padding-top: 8px;
  }
}

.profile__link {
  display: flex;
  margin-bottom: 20px;

  color: $color-rich-blue;
  text-align: left;
  transition: color $default-transition-settings;

  &:focus,
  &:hover,
  &:active {
    color: $color-blackcurrant;
  }

  &--margin {
    margin-bottom: 10px;
    margin-left: 20px;
  }
}

.profile__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 792px;
  width: 100%;
  padding: 20px;

  background-color: $color-snow;
  border-radius: 10px;

  @include viewport--av {
    position: relative;
    max-width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &--col {
    flex-direction: column;
  }

  &--sm {
    padding-bottom: 16px;

    @include viewport--av {
      padding: 20px 5.33vw;
    }
  }
}

.profile__data {
  min-width: 300px;
  width: 366px;

  @include viewport--av {
    order: 2;
    min-width: 100%;
    width: 100%;
  }

  a {
    margin-left: 20px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  .choices {
    @include viewport--av {
      margin-left: 0;
      max-width: 100%;
    }
  }

  .choices__inner {
    @include viewport--av {
      margin-left: 0;
      max-width: 100%;
    }
  }
}

.profile__tooltip {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 400px;
  margin-bottom: 25px;

  &--big {
    margin-bottom: 0;
  }

  img {
    width: 22px;
    height: 23px;
    margin-right: 13px;

    svg {
      fill: red;
    }
  }

  p {
    margin: 0;
    padding: 0;
  }
}

.profile__row {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 366px;
  width: 100%;
  height: 60px;
  margin-bottom: 10px;
  padding: 10px 27px 12px 20.5px;

  border: 1px solid $color-selago-1;
  border-radius: 10px;
  background-color: $color-default-white;

  span {
    font-size: 12px;
    line-height: 15px;
    color: $color-rum;
  }

  p {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 23px;
  }
}

.profile__row-box {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;

  @include viewport--av {
    flex-direction: column;
    justify-content: center;
  }

  &--map {
    height: 252px;
    margin-top: 10px;

    border-radius: 10px;
    overflow: hidden;
    backface-visibility: hidden;
    z-index: 1;
  }

  .choices {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 10px;
    }

    @include viewport--lg {
      max-width: 49%;
    }

    @include viewport--sm {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 20px;
      }
    }

    @include viewport--av {
      max-width: 100%;
    }
  }

  .choices__inner {
    @include viewport--av {
      max-width: 100%;
      margin: 0;
    }
  }
}

.profile__info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 366px;
  width: 100%;
  height: 80px;
  padding: 22px 18.96px 22px 22.76px;

  border-radius: 10px;
  background-color: $color-lavender;

  @include viewport--lg {
    max-width: 100%;
    align-items: center;
  }

  p {
    max-width: 200px;
    width: 100%;
    margin: 0;
    padding: 0;

    @include viewport--lg {
      max-width: 100%;
      margin-right: 10px;
    }

    @include viewport--xs {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &--add {
    align-items: center;
    max-width: 335px;
    height: 60px;
    padding: 0;
    background-color: transparent;
  }
}

.profile__info-link {
  max-width: 110px;
  max-height: 40px;

  @include viewport--lg {
    max-width: 250px;
    width: 100%;
  }

  @include viewport--sm {
    max-width: 100px;
    width: 100%;
  }
}

.profile__info-link-add {
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin: 0;
  margin-top: 10px;
  padding: 19.2px 29.33px 22px 23px;

  background-color: $color-snow;
  background-image: repeating-linear-gradient(0deg, $color-prelude, $color-prelude 10px, transparent 10px, transparent 20px, $color-prelude 20px), repeating-linear-gradient(90deg, $color-prelude, $color-prelude 10px, transparent 10px, transparent 20px, $color-prelude 20px), repeating-linear-gradient(180deg, $color-prelude, $color-prelude 10px, transparent 10px, transparent 20px, $color-prelude 20px), repeating-linear-gradient(270deg, $color-prelude, $color-prelude 10px, transparent 10px, transparent 20px, $color-prelude 20px);
  background-size: 1px 100%, 100% 1px, 1px 100%, 100% 1px;
  background-position: 0 0, 0 0, 100% 0, 0 100%;
  background-repeat: no-repeat;
  transition: background-color $default-transition-settings;

  @include viewport--lg {
    max-width: 80%;
  }

  @include viewport--sm {
    margin-top: 0;
  }

  @include viewport--av {
    max-width: 100%;
  }

  @include viewport--xs {
    font-size: 12px;
    line-height: 16px;
  }

  &:hover,
  &:focus,
  &:active {
    background-color: $color-default-white;
  }

  &::before {
    position: absolute;
    top: 24.5px;
    right: 15px;
    content: "";
    width: 8px;
    height: 8px;
    background-image: url("../img/svg/plus.svg");
  }
}

.profile__button-box {
  display: flex;
  flex-direction: row;
  max-width: 450px;

  @include viewport--sm {
    width: 100%;
    justify-content: space-between;
    padding-left: 5.33vw;
    padding-right: 5.33vw;
  }
}

.profile__remove {
  width: 213px;
  height: 45px;

  @include viewport--sm {
    width: 100%;
  }
}

.profile__save {
  width: 204px;
  margin-right: 10px;
}

.profile__cancel {
  width: 117px;
}
