.main-nav__list {
  display: flex;
  flex-direction: column;
  min-height: max-content;
  padding: 0;
  margin: 0;

  list-style: none;

  li:last-of-type {
    @include viewport--av {
      // для ios
      margin-bottom: 200px;
    }
  }

  @include viewport--md {
    flex-basis: 100%;
  }
}

.main-nav__link {
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
  padding: 14px 9px 11px;

  border-radius: 10px;

  transition: color $default-transition-settings;

  &[href]:focus,
  &[href]:hover {
    color: $color-rich-blue;
  }

  &[href]:active {
    color: $color-blackcurrant;
  }

  &--active {
    background-color: $color-lavender;

    svg {
      color: $color-rich-blue;
    }
  }

  svg {
    margin-top: -2px;
    margin-right: 11px;
  }
}
